  --granite-gray: rgb(103, 103, 103);
  --alice-blue: rgb(240, 248, 255);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: none;
  background-color: inherit;
  width: 100%;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
}

button:hover {
  background-color: var(--granite-gray);
  color: whitesmoke;
}

i {
  margin-right: 10px;
}

/* Header */

header {
  display: flex;
  background-color: var(--granite-gray);
  width: 100%;
  justify-content: space-between;
  padding: 20px;
  color: whitesmoke;
}

.logo {
  display: flex;
  gap: 20px;
  align-items: center;
}

.logo-img {
  width: 40px;
  height: 40px;
}

.page-title {
  font-size: 38px;
  font-weight: bold;
  font-family: Merriweather;
}

.open-nav-btn {
  display: none;
  background-color: var(--granite-gray);
  border: none;
  width: fit-content;
  font-size: 40px;
  justify-content: center;
  align-items: center;
}

.nav-icon {
  display: block;
  width: 40px;
  height: 40px;
}

/* Navigation */
main {
  display: flex;
  flex-wrap: wrap;
  min-height: calc(100vh - 87px);
  font-family: Roboto, sans-serif;
}

.nav {
  width: 300px;
  min-width: 250px;
  padding: 20px;
  background-color: var(--alice-blue);
}

.nav.active {
  display: block;
  width: 100%;
}

.default-project-btn {
  text-align: left;
}

.projects-section {
  padding: 10px;
  margin-top: 10px;
  font-size: 25px;
  font-weight: bold;
}

.add-project-btn {
  text-align: left;
  padding: 20px;
  font-weight: bold;
}

#project-form {
  display: none;
  padding: 10px;
}

#project-details-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

#project-name {
  box-sizing: border-box;
  padding: 10px;
  width: 100%;
  border-radius: 5px;
  text-align: center;
  border: 1px solid var(--granite-gray);
}

.project-form-btns {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  gap: 10px;
}

.project-form-btns button {
  border: 1px solid var(--granite-gray);
}

#submit-project-btn {
  background-color: rgb(148, 245, 148);
}

#cancel-project-btn {
  background-color: rgb(248, 185, 185);
}

.project {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project:hover {
  background-color: var(--granite-gray);
  color: whitesmoke;
}

.project:hover .delete-project-btn {
  display: block;
}

.project-btn {
  text-align: left;
}

.delete-project-btn {
  width: auto;
  display: none;
  color: whitesmoke;
}

/* Project Container */

.project-container {
  display: flex;
  flex-direction: column;
  justify-content: top;
  align-items: center;
  flex: 1;
  min-width: 300px;
  padding: 20px;
}

#project-header {
  font-size: 24px;
  font-weight: bold;
  font-family: Merriweather;
  border-bottom: 1px solid black;
  margin-bottom: 20px;
}

.add-todo-btn {
  width: auto;
  padding: 10px 60px;
}

#todo-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: fit-content;
  padding: 0;
}

.modal-header {
  border-bottom: 1px solid gray;
  background-color: var(--granite-gray);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  color: whitesmoke;
}

.close-modal-btn {
  width: auto;
  font-size: 20px;
  font-weight: bold;
  font-family: Merriweather, Arial;
  color: whitesmoke;
}

.close-modal-btn:hover {
  background-color: transparent;
  color: inherit;
}

.modal-content {
  padding: 20px;
}

#todo-form {
  display: flex;
  flex-direction: column;
}

#todo-title-input,
#todo-description-input,
#due-date-label,
#priority-label {
  border: none;
  padding: 10px;
  max-width: 100%;
  font-size: 20px;
}

#due-date-input,
#priority-input {
  font-size: 15px;
  border: 1px solid black;
  border-radius: 5px;
}

.submit-modal {
  display: flex;
  justify-content: center;
}

.submit-todo-btn {
  width: 80%;

  align-self: center;
  margin-bottom: 20px;
}

/* todos-container */

.todos-container {
  width: clamp(300px, 75%, 1500px);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.todo-item {
  width: 90%;
  display: flex;
  justify-content: space-between;
  background-color: rgb(231, 231, 231);
  padding: 10px;
}

.todo-item.Medium {
  border-left: 3px solid green;
}

.todo-item.High {
  border-left: 3px solid red;
}

.todo-item.Low {
  border-left: 3px solid orange;
}

.todo-left,
.todo-right {
  display: flex;
  align-items: center;
  gap: 15px;
}

.edit-btn,
.delete-todo-btn {
  width: 100%;
  padding: 0;
}

.fa-pen-to-square,
.fa-delete-left {
  margin: 0;
}

@media (max-width: 650px) {
  .open-nav-btn {
    display: block;
  }

  .nav {
    width: 100%;
    display: none;
  }
}
